import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {FormsModule} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

//Router module used for setting up the application level route 
import {RouterModule,Routes} from '@angular/router';

import { ToastrModule } from 'ngx-toastr';


import { AppComponent } from './app.component';
import { HomeComponent } from './home/home.component';
import { ProductComponent } from './product/product.component';
import { AboutComponent } from './about/about.component';
import { ContactComponent } from './contact/contact.component';
import {NotFoundComponent} from "./not-found/not-found.component";
import { SingleProductViewComponent } from './single-product-view/single-product-view.component';
import { ProductListViewComponent } from './product-list-view/product-list-view.component';
import { ProductCreateComponent } from './product-create/product-create.component';
import { ProductEditComponent } from './product-edit/product-edit.component';
import { ProductDeleteComponent } from './product-delete/product-delete.component';


@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    ProductComponent,
    AboutComponent,
    ContactComponent,
    NotFoundComponent,
    SingleProductViewComponent,
    ProductListViewComponent,
    ProductCreateComponent,
    ProductEditComponent,
    ProductDeleteComponent
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    FormsModule,
    BrowserAnimationsModule,
    ToastrModule.forRoot({
      timeOut: 5000,
      positionClass: 'toast-top-right',
      preventDuplicates: true,
    }),
    RouterModule.forRoot([
      {path:'home',component:HomeComponent},
      {path:'',component:HomeComponent,pathMatch:'full'},
      {path:'about',component:AboutComponent},
      {path:'product',component:ProductComponent},
      {path:'contact',component:ContactComponent},
      {path:'product-list',component:ProductListViewComponent},
      {path:'product/view/:id',component:SingleProductViewComponent},
      {path:'product/create',component:ProductCreateComponent},
      {path:"**",component:NotFoundComponent}
        ])
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }
